<template>
  <div class="docs">
    <header-view></header-view>

    <div class="doc-main">

      <template>
        <div class="menu">
          <el-menu :default-active="activeIndex" class="el-menu-vertical-demo">
            <el-submenu v-for="(floderItem, floderIndex) in mdList" :key="floderIndex.id" :index="floderIndex + ''">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>{{ floderItem.filename }}</span>
              </template>

              <el-menu-item v-for="(mdItem, mdIndex) in floderItem.details" :key="mdItem.id"
                :index="floderItem.id + '-' + mdIndex" @click="menuClick(mdItem, mdIndex, floderIndex, floderItem)">
                {{ mdItem.mdname }}
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </div>

        <div class="markdown-body">
          <!--  -->
          <div class="details">
            <component :is="componentList[activeComponent]" class="markdown-body"></component>
          </div>
        </div>
        <el-backtop>
          <div class="up">UP</div>
        </el-backtop>
      </template>

    </div>
  </div>
</template>

<script>
// /docsmd
const mdComponent = require.context('@/docs',true,/\.(md)$/)

const components = {}; // md
const filterCmps = [];

mdComponent.keys().forEach((fileName,index) => {
  let component = mdComponent(fileName).default
  !filterCmps.includes(fileName) && (components["mdComponent" + index] = component)
})

import HeaderView from "@/components/Header.vue";

export default {
  data () {
    return {
      mdList: [], // (json)
      activeComponent: 0, // md
      activeIndex: '0-0', // el-menuindex
      //  name  xxx_${index}
      // 
      componentList: Object.keys(components).sort(
        (a,b) => a.split('_')[1] - b.split('_')[1]
      )
    }
  },
  components: {
    HeaderView,
    ...components
  },
  mounted () {
    this.getMdFileName(); // md

    this.activeComponent = localStorage.getItem("activeComponent")
    this.activeIndex = localStorage.getItem("activeIndex")
  },
  methods: {
    // md
    // 
    // 
    getMdFileName () {
      let list = mdComponent.keys();
      list.forEach((value,index,arr) => {
        const pos = value.lastIndexOf('/');
        value = value.substr(pos + 1);
        value = value.replace(".md","");
      })

      // fileList
      // [
      //   {  "id": 0,"filename": "Element","mdname": "select"},
      //   {  "id": 1,  "filename": "Element",  "mdname": "dialog"},
      //   ...
      // ]
      let fileList = [];
      list.forEach((value,index,arr) => {
        // ./Element/select.md
        value = value.replace("./",""); //  ./
        value = value.replace(".md",""); //  .md
        // Element/select

        const pos = value.lastIndexOf('/'); //  / 
        const filename = value.substring(0,pos); // 
        const mdname = value.substring(pos + 1); // 

        const obj = {
          id: index,
          filename: filename,
          mdname: mdname
        }
        fileList.push(obj); // fileList
      })

      // 
      // [
      //   {
      //     "id": 0,
      //     "filename": "Element",
      //     "details": [
      //       {
      //         "id": 0,
      //         "filename": "Element",
      //         "mdname": "select"
      //       },
      //       {
      //         "id": 1,
      //         "filename": "Element",
      //         "mdname": "dialog"
      //       },
      //       {
      //         "id": 2,
      //         "filename": "Element",
      //         "mdname": ""
      //       },
      //       {
      //         "id": 3,
      //         "filename": "Element",
      //         "mdname": "pagination"
      //       },
      //       {
      //         "id": 4,
      //         "filename": "Element",
      //         "mdname": ""
      //       }
      //     ]
      //   },
      //   {
      //     "id": 1,
      //     "filename": "css",
      //     "details": [
      //       {
      //         "id": 5,
      //         "filename": "css",
      //         "mdname": "css"
      //       },
      //       {
      //         "id": 6,
      //         "filename": "css",
      //         "mdname": "vue"
      //       },
      //       {
      //         "id": 7,
      //         "filename": "css",
      //         "mdname": ""
      //       }
      //     ]
      //   },
      //   {
      //     "id": 2,
      //     "filename": "node",
      //     "details": [
      //       {
      //         "id": 8,
      //         "filename": "node",
      //         "mdname": "nvmnode"
      //       }
      //     ]
      //   },
      //   {
      //     "id": 3,
      //     "filename": "other",
      //     "details": [
      //       {
      //         "id": 9,
      //         "filename": "other",
      //         "mdname": ""
      //       }
      //     ]
      //   },
      //   {
      //     "id": 4,
      //     "filename": "vue",
      //     "details": [
      //       {
      //         "id": 10,
      //         "filename": "vue",
      //         "mdname": "vueanimate"
      //       }
      //     ]
      //   }
      // ]
      let fileJsonList = []; // 
      let outsideIndex = 0; // indexid
      let indideIndex = 0; // indexid
      for (let index = 0; index < fileList.length; index++) {
        // 
        if (index == 0) {
          const obj = {
            id: outsideIndex,
            filename: fileList[index].filename,
            details: [fileList[index]],
          }
          fileJsonList[outsideIndex] = obj;
          indideIndex += 1;
        }
        // 
        if (index != 0 && fileList[index].filename == fileList[index - 1].filename) {
          fileJsonList[outsideIndex].details.push(fileList[index])
        }
        // 
        if (index != 0 && fileList[index].filename != fileList[index - 1].filename) {
          outsideIndex++;
          const obj = {
            id: outsideIndex,
            filename: fileList[index].filename,
            details: [fileList[index]],
          }
          fileJsonList[outsideIndex] = obj;
          indideIndex = 0;
        }
      }
      this.mdList = fileJsonList;
    },

    menuClick (mdItem,mdIndex,floderIndex,floderItem) {
      this.activeComponent = mdItem.id;
      this.activeIndex = floderIndex + '-' + mdIndex;
      localStorage.setItem("activeComponent",this.activeComponent);
      localStorage.setItem("activeIndex",this.activeIndex);
    },
  },
}
</script>

